@import url(https://fonts.googleapis.com/css2?family=Barrio);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Eater&family=Slackey&display=swap);
body {
  padding: 0;
  margin: 0;
  background-image: url(/New-Movie/static/media/cinema.0906ba21.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.grid-container {
  position: relative;
  display: grid;
  grid-template-columns: 250px 200px;
  grid-auto-rows: auto;
  grid-template-areas:
    "title title "
    "poster  synopsis "
    "rating release ";
  grid-gap: 10px;
  width: 450px;
  height: 100%;
  left: 10%;
  top: 50%;
  -webkit-transform: translate(500px, 62px);
          transform: translate(500px, 62px);
  background-color: linear-gradient(#f3904f, 3b4371);
}
#title {
  font-size: 18px;
  text-align: center;
  grid-area: title;
  background-color: darkorange;
  font-family: "Barrio", cursive;
  height: 60px;
  padding: 15px;
}
#synopsis {
  font-size: 12px;
  grid-area: synopsis;
  background-color: yellow;
  padding: 10px;
  font-family: "Barrio", cursive;
}
#rating {
  font-family: "Barrio", cursive;
  font-size: 14px;
  text-align: center;
  grid-area: rating;
  background-color: white;
  top: 25px;
  width: 100%;
}
#poster {
  grid-area: poster;
}
#releaseDate {
  grid-area: release;
  font-family: "Barrio", cursive;
  font-size: 14px;
  background-color: powderblue;
  text-align: center;
}

#formContainer {
  position: absolute;
  left: 18%;
  top: 42%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: bisque;
  box-shadow: 40px -16px teal;
  border-radius: 12px;
  width: 310px;
  height: 115px;
}
form {
  position: absolute;
  left: 51%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 280px;
  height: 100px;
}
#inputMovie {
  width: 200px;
  height: 40px;

  font-weight: lighter;
  position: -webkit-sticky;
  position: sticky;
  left: 10px;
  top: 0px;
  padding: 5px;
}

#submitSearchForm {
  position: -webkit-sticky;
  position: sticky;
  font-size: 10px;
  height: 20px;
  width: 120px;
  left: 10px;
}

.containerCard {
  position: absolute;
  top: 30%;
  left: 60%;
  -webkit-transform: translate(0%, -10%);
          transform: translate(0%, -10%);
  background-color: transparent;
  width: 200px;
  height: 300px;
}
.movieCard {
  position: absolute;
  top: 16%;
  left: 0%;
  -webkit-transform: translate(0%, -10%);
          transform: translate(0%, -10%);
  width: 200px;
  height: 250px;
}

#lead {
  color: #000;
  font-size: 14px;
  padding: 2px;
  font-family: "Dancing Script", cursive;
  text-overflow: ellipsis;
}
#reviewDate {
  color: #000;
  font-size: 9px;
  padding: 2px;
}

#publisher {
  font-family: "Lobster", cursive;
  font-size: 18px;
  padding: 2px;
  text-overflow: ellipsis;
}
.flipCardInner {
  position: relative;
  -webkit-transition: -webkit-transform 0.8s;
  transition: -webkit-transform 0.8s;
  transition: transform 0.8s;
  transition: transform 0.8s, -webkit-transform 0.8s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}
.movieCard:hover .flipCardInner {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}
.flipCardFront,
.flipCardBack {
  position: absolute;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}
.flipCardFront {
  background: #dcdcdc;
  color: black;
  width: 200px;
  height: 250px;
}
.flipCardBack {
  position: absolute;
  top: 16%;
  left: 0%;
  -webkit-transform: translate(0%, -10%);
          transform: translate(0%, -10%);
  width: 200px;
  height: 250px;
  background-color: dodgerblue;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}
img {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 120px;
  height: 170px;
}

#formContainerReview {
  left: 24%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  position: absolute;
  background: -webkit-gradient(linear, left top, left bottom, from(#833ab4), color-stop(#1dfddb), to(#fcb045));
  background: -webkit-linear-gradient(#833ab4, #1dfddb, #fcb045);
  background: linear-gradient(#833ab4, #1dfddb, #fcb045);
  width: 395px;
  height: 380px;
}

#formControl {
  position: absolute;
  left: 57%;
  top: 54%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 400px;
  height: 380px;
}
#inputTitle {
  position: absolute;
  width: 80%;
  height: 30px;
  left: 9px;
  border: solid slategray 3px;
}
#inputReviewBody {
  position: absolute;
  width: 80%;
  height: 200px;
  left: 9px;
  top: 60px;
  border: solid slategray 3px;
  word-break: break-all;
}
#submitReview {
  word-break: break-all;
  position: absolute;
  width: 100px;
  height: 40px;
  left: 9px;
  top: 300px;
  text-align: center;
  border: solid slategray 3px;
  background-color: rgb(110, 47, 47);
  color: #fff;
}

nav {
  width: 100%;
  height: 110px;
  padding: 0;
  margin: 0;
  background: url(/New-Movie/static/media/cinema2.f29334c5.jpg);
  background-size: cover;
  background-repeat: no-repeat;
}

#theMovieDbImage {
  width: 50%;
  height: 80px;
  /* text-align: center; */
}

#reviewHeader {
  font-family: "Slackey", cursive;
  font-size: 20px;
  text-align: center;
}
#searchHeader {
  font-family: "Slackey", cursive;
  font-size: 20px;
  text-align: center;
}
#movieSearchApp {
  color: transparent;
  position: relative;
  font-family: "Eater", cursive;
  font-size: 45px;
  left: 100px;
  top: 20px;
  background-position: 0 50%;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-transition: text-shadow 0.35s linear;
  transition: text-shadow 0.35s linear;
  text-shadow: -1px -1px 0 #ffffff, 1px -1px 0 #ffffff, -1px 1px 0 #ffffff,
    1px 1px 0 #ffffff;
}
#movieSearchApp:hover {
  text-shadow: 0 0 10px rgba(221, 221, 221, 1), 0 0 0 #ddd;
}

/* #reviewHeader:hover {
  text-decoration: underline solid 3px gold;
} */

#reviewHeader,
#searchHeader,
#theMovieDbLink,
#theMovieDbImage {
  position: relative;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

#listItems {
  display: flex;
  height: 100%;
  width: 50%;
  left: 0%;
}

#listItems li {
  display: flex;
  height: 100%;

  /* justify-content: center; */
  background: #ddd;
  margin-left: 10px;
}

#footerContainer {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 40px;
  background: #e0e864;
}
#artwork {
  font-style: italic;
  font-size: 10px;
  position: absolute;
  font-weight: 900;
  left: 50%;
  top: 40%;
}

